const Tags = ({ list }) => {
  const tagList = list.slice(0, Math.min(list.length, 3));

  return (
    <div className="app_tags">
      {tagList.join("  ")}
      {/* {tagList.map((tag, index) => {
        return (
          <div className="app_tag" key={`tag_${index}`}>
            {tag}
          </div>
        );
      })} */}
    </div>
  );
};

export default Tags;
