import { useQuery } from "@tanstack/react-query";
import { useState } from "react";
import fetchImage from "./fetchImage";

const AppImage = ({ id, link, linkReddit, isMobile }) => {
  //const [src, setSrc] = useState("./placeholder.svg");
  const [offset, setOffset] = useState(0);

  const isLocal =
    window.location.href.startsWith("http://192.") ||
    window.location.href.startsWith("http://localhost:");

  let cdnUrl = `https://vrsortcdn.b-cdn.net/thumbnails/${id}.jpg`;
  if (isLocal) {
    cdnUrl = "6121529404565492.jpg";
  }
  const { data } = useQuery([id, linkReddit], () => fetchImage(cdnUrl, link));

  if (!data) {
    return <img className="image" src="placeholder.svg" alt="" />;
  }
  const [index, src] = data;

  let startTouchPosition = { x: 0, y: 0 };
  let lastTouchPosition = { x: 0, y: 0 };

  if (index === 1) {
    return <img className="image" src={src} alt={""} />;
  }

  return (
    <div
      className="image_container"
      onTouchStart={(e) => {
        startTouchPosition.x = e.targetTouches[0].pageX;
        startTouchPosition.y = e.targetTouches[0].pageY;
      }}
      onTouchEnd={(e) => {
        const delta = lastTouchPosition.x - startTouchPosition.x;

        if (Math.abs(delta) > 10) {
          const sign = Math.sign(delta);
          const newOffset = offset + sign * 25;

          if (newOffset < -75) {
            setOffset(0);
          } else if (newOffset > 0) {
            setOffset(-75);
          } else {
            setOffset(newOffset);
          }
        }
      }}
      onTouchMove={(e) => {
        lastTouchPosition.x = e.targetTouches[0].pageX;
        lastTouchPosition.y = e.targetTouches[0].pageY;
      }}
    >
      {!isMobile && (
        <div
          className="hover_zones"
          onPointerLeave={() => {
            setOffset(0);
          }}
        >
          <div
            onPointerEnter={() => {
              setOffset(0);
            }}
          ></div>
          <div
            onPointerEnter={() => {
              setOffset(-25);
            }}
          ></div>
          <div
            onPointerEnter={() => {
              setOffset(-50);
            }}
          ></div>
          <div
            onPointerEnter={() => {
              setOffset(-75);
            }}
          ></div>
        </div>
      )}

      {isMobile && (
        <div className="carousel_dots">
          <div className={`dot ${offset === 0 ? "active" : ""}`}></div>
          <div className={`dot ${offset === -25 ? "active" : ""}`}></div>
          <div className={`dot ${offset === -50 ? "active" : ""}`}></div>
          <div className={`dot ${offset === -75 ? "active" : ""}`}></div>
        </div>
      )}
      <img
        className="image"
        src={src}
        style={{ transform: "translateX(" + offset + "%)" }}
        alt={""}
      />
    </div>
  );
};
export default AppImage;
