const FilterOneChoice = ({
  filterTitle,
  filterId,
  filterItems,
  filters,
  setFilters,
}) => {
  return (
    <div className="filter">
      <div className="filter_name">{filterTitle}</div>
      <div id={filterId}>
        {filterItems.map((item, index) => {
          return (
            <div
              key={filterId + "_" + item.value}
              className={`radio_option ${
                item.value === filters[filterId].value ? "active" : ""
              }`}
              data-value={item.value}
              onClick={() => {
                setFilters({
                  ...filters,
                  [filterId]: item,
                });
              }}
            >
              <div className="radio"></div>
              <div className="radioName">{item.name}</div>
            </div>
          );
        })}
      </div>
    </div>
  );
};
export default FilterOneChoice;
