const Stars = ({ value, number }) => {
  const starsArray = Array(5);

  for (let i = 0; i < starsArray.length; ++i) {
    starsArray[i] = i <= value;
  }

  if (+number === 0) {
    return (
      <div className="app_rating">
        <div className="stars norating">
          {starsArray.map((item, index) => {
            return <div key={`star_${index}`} className="star"></div>;
          })}
        </div>
        <span>(no reviews)</span>
      </div>
    );
  }

  return (
    <div className="app_rating">
      <div className="stars">
        {starsArray.map((item, index) => {
          return item ? (
            <div key={`star_${index}`} className="star"></div>
          ) : (
            <div key={`star_${index}`} className="star empty"></div>
          );
        })}
      </div>
      <span>({number})</span>
    </div>
  );
};

export default Stars;
