import Card from "./Components/AppCard/Card";

import { AutoSizer, List, WindowScroller } from "react-virtualized";
import "react-virtualized/styles.css";
import { Collection } from "react-virtualized";
import { useEffect, useRef } from "react";
import React, { useLayoutEffect, useState } from "react";

function useWindowSize() {
  const [size, setSize] = useState([0, 0]);
  useLayoutEffect(() => {
    function updateSize() {
      setSize([window.innerWidth, window.innerHeight]);
    }
    window.addEventListener("resize", updateSize);
    updateSize();
    return () => window.removeEventListener("resize", updateSize);
  }, []);
  return size;
}

const AppList = (props) => {
  const [cardsLayout, setCardsLayout] = useState({
    numColumns: 4,
    width: 250,
    height: 250,
    gridWidth: 4 * 250 + (4 - 1) * 15,
  });
  const [windowWidth, _] = useWindowSize();

  const collectionRef = useRef();
  useEffect(() => {
    let tmpCardsLayout = {
      numColumns: 4,
      width: 250,
      height: 250,
      gridWidth: 4 * 250 + (4 - 1) * 15,
    };

    if (windowWidth < 1380) {
      let sideBarWidth = 260;
      let padding = 48;
      if (1200 <= windowWidth) {
        tmpCardsLayout.numColumns = 4;
      }

      if (windowWidth < 1200) {
        tmpCardsLayout.numColumns = 4;
      }
      if (windowWidth < 1024) {
        tmpCardsLayout.numColumns = 3;
        sideBarWidth = 290;
      }

      if (windowWidth <= 820) {
        tmpCardsLayout.numColumns = 3;
        sideBarWidth = 0;
      }
      if (windowWidth < 520) {
        tmpCardsLayout.numColumns = 2;
        padding = 48;
      }
      if (windowWidth < 450) {
        tmpCardsLayout.numColumns = 1;
      }

      tmpCardsLayout.width =
        (windowWidth -
          sideBarWidth -
          padding -
          (tmpCardsLayout.numColumns - 1) * 15) /
        tmpCardsLayout.numColumns;

      const imageHeiht = tmpCardsLayout.width / (144 / 81);
      tmpCardsLayout.height = imageHeiht + 110;
    }
    tmpCardsLayout.gridWidth =
      tmpCardsLayout.numColumns * tmpCardsLayout.width +
      (tmpCardsLayout.numColumns - 1) * 15;
    document.getElementById("app_list_list").style.width =
      tmpCardsLayout.gridWidth + "px";

    setCardsLayout(tmpCardsLayout);
    collectionRef.current.recomputeCellSizesAndPositions();
  }, [windowWidth]);

  const cellRenderer = ({ index, key, style }) => {
    const app = props.apps[index];
    return (
      <Card {...app} style={style} itemKey={key} isMobile={windowWidth < 520} />
    );
  };

  function cellSizeAndPositionGetter({ index }) {
    return {
      height: cardsLayout.height,
      width: cardsLayout.width,
      x: (index % cardsLayout.numColumns) * (cardsLayout.width + 15),
      y: Math.floor(index / cardsLayout.numColumns) * (cardsLayout.height + 15),
    };
  }

  return (
    <WindowScroller>
      {({ height, isScrolling, registerChild, scrollTop }) => (
        <div id="app_list_list">
          {props.children}

          <AutoSizer disableHeight>
            {({ width }) => (
              <Collection
                ref={collectionRef}
                // ref={(el) => {
                //   window.listEl = el;
                // }}
                autoHeight
                isScrolling={isScrolling}
                scrollTop={scrollTop}
                cellCount={props.apps.length}
                cellRenderer={cellRenderer}
                cellSizeAndPositionGetter={cellSizeAndPositionGetter}
                height={height}
                width={width}
              />
            )}
          </AutoSizer>
        </div>
      )}
    </WindowScroller>
  );

  // return (
  //   <WindowScroller>
  //     {({ height, isScrolling, registerChild, scrollTop }) => (
  //       <div id="app_list_list">
  //         {props.children}

  //         <AutoSizer disableHeight>
  //           {({ width }) => (
  //             <div ref={registerChild}>
  //               <List
  //                 ref={(el) => {
  //                   window.listEl = el;
  //                 }}
  //                 autoHeight
  //                 height={height}
  //                 isScrolling={isScrolling}
  //                 rowCount={props.apps.length}
  //                 overscanRowCount={2}
  //                 rowHeight={100}
  //                 rowRenderer={Row}
  //                 scrollTop={scrollTop}
  //                 width={width}
  //               />
  //             </div>
  //           )}
  //         </AutoSizer>
  //       </div>
  //     )}
  //   </WindowScroller>
  // );
};

export default AppList;
