import fetchApps from "./fetchApps";
import {
  useQuery,
  QueryClient,
  QueryClientProvider,
} from "@tanstack/react-query";
import AppList from "./AppList";
import "./style.css";
import "./assets/style.css";
import FilterAddRemove from "./Components/Filters/FilterAddRemove";
import FilterOneChoice from "./Components/Filters/FilterOneChoice";
import Sort from "./Components/Sort/Sort";
import Header from "./Layout/Header";
import { useState } from "react";
import { useEffect } from "react";
const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      staleTime: Infinity,
    },
  },
});

const todaySeconds = Date.now() / 1000;
const secondsInADay = 60 * 60 * 24;
const last7Days = todaySeconds - secondsInADay * 7;
const last14Days = todaySeconds - secondsInADay * 14;
const last30Days = todaySeconds - secondsInADay * 30;

const defaultFilters = {
  filter_feature: [],
  filter_price: { value: "all", name: "All" },
  filter_time: { value: "all", name: "All" },
  filter_rating: { value: "all", name: "All" },
  filter_cat: { value: "all", name: "All" },
  filter_iarc: { value: "all", name: "All" },
  filter_input: { value: "all", name: "All" },
  filter_search: { value: "" },
};

const sortOptions = [
  { key: "time", dir: 1, name: "Old First" },
  { key: "time", dir: -1, name: "New First" },

  { key: "price", dir: 1, name: "Lowest Price" },
  { key: "price", dir: -1, name: "Highest Price" },

  { key: "reviewsm", dir: -1, name: "User Reviews" },
  { key: "reviewsh", dir: -1, name: "Weighted Reviews" },

  { key: "size", dir: -1, name: " Size (large)" },
  { key: "size", dir: 1, name: "Size (small)" },
];

const categoriesOptions = [
  { value: "all", name: "All" },
  { value: "games", name: "Games" },
  { value: "apps", name: "Apps" },
  { value: "entertainment", name: "Entertainment" },
];
const filter_time_options = [
  { value: "all", name: "All" },
  { value: 7, name: "Last 7 days" },
  { value: 14, name: "Last 14 days" },
  { value: 30, name: "Last 30 days" },
];

const filter_time_times = { 7: last7Days, 14: last14Days, 30: last30Days };

const defaultSort = {
  key: "time",
  dir: -1,
  name: "New First",
};
function AppChild() {
  const [filters, setFilters] = useState(defaultFilters);
  const [activeSort, setSort] = useState(defaultSort);
  const [questBrowser, setQuestBrowser] = useState(false);
  useEffect(() => {
    function get_url_params() {
      const queryString = window.location.search.toLowerCase();

      const urlParams = new URLSearchParams(queryString);

      let tmpSort = JSON.parse(JSON.stringify(defaultSort));
      let tmpFilters = JSON.parse(JSON.stringify(defaultFilters));

      if (urlParams.has("t")) {
        for (let i = 0; i < filter_time_options.length; ++i) {
          if (filter_time_options[i].value === +urlParams.get("t")) {
            tmpFilters.filter_time = filter_time_options[i];
            break;
          }
        }
      }
      if (urlParams.has("cat")) {
        for (let i = 0; i < categoriesOptions.length; ++i) {
          if (categoriesOptions[i].value === urlParams.get("cat")) {
            tmpFilters.filter_cat = categoriesOptions[i];
            break;
          }
        }
      }
      if (urlParams.has("rating")) {
        tmpFilters.filter_rating.value = urlParams.get("rating");
      }
      if (urlParams.has("price")) {
        tmpFilters.filter_price.value = urlParams.get("price");
      }

      if (urlParams.has("iarc")) {
        tmpFilters.filter_iarc.value = +urlParams.get("iarc");
      }
      if (urlParams.has("q")) {
        let searchString = urlParams.get("q");
        let searchTerms = searchString.split("||");

        tmpFilters.filter_search.value = searchTerms;
      }

      let hasSort = false;
      if (urlParams.has("sort")) {
        tmpSort.key = urlParams.get("sort");
        hasSort = true;
      }

      if (urlParams.has("sortdir")) {
        tmpSort.dir = +urlParams.get("sortdir");
        hasSort = true;
      }

      if (hasSort) {
        for (let i = 0; i < sortOptions.length; ++i) {
          if (
            sortOptions[i].key === tmpSort.key &&
            sortOptions[i].dir === tmpSort.dir
          ) {
            tmpSort.name = sortOptions[i].name;
            break;
          }
        }
      }

      if (urlParams.has("with")) {
        let filters_temp = urlParams.get("with").split(",");
        for (let i = 0; i < filters_temp.length; i++) {
          tmpFilters.filter_feature[filters_temp[i]] = 1;
        }
      }
      if (urlParams.has("without")) {
        let filters_temp = urlParams.get("without").split(",");
        for (let i = 0; i < filters_temp.length; i++) {
          tmpFilters.filter_feature[filters_temp[i]] = -1;
        }
      }

      setSort(tmpSort);
      setFilters(tmpFilters);
    }

    get_url_params();
    // document.getElementById("device").innerHTML =
    //   window.navigator.userAgent +
    //   "<br>" +
    //   window.innerWidth +
    //   "x" +
    //   window.innerHeight;

    if (window.navigator.userAgent.indexOf("oculus") !== -1) {
      setQuestBrowser(true);
    }
  }, []);

  useEffect(() => {
    update_url();
  }, [activeSort, filters]);

  function compareObjects(obj1, obj2) {
    for (const [key, value] of Object.entries(obj1)) {
      if (!obj2.hasOwnProperty(key)) {
        return false;
      } else {
        if (Array.isArray(value) && Array.isArray(obj2[key])) {
          if (value.length === 0 && obj2[key].length !== 0) {
            return false;
          }
        } else if (typeof value === "object") {
          if (!compareObjects(obj2[key], value)) {
            return false;
          }
        } else {
          return obj2[key] === value;
        }
      }
    }
    return true;
  }
  function update_url() {
    console.log("update url");
    console.log(filters, defaultFilters);
    if (
      compareObjects(filters, defaultFilters) &&
      compareObjects(activeSort, defaultSort)
    ) {
      console.log("defaults");
      return;
    }

    const urlParams = new URLSearchParams();
    if (filters.filter_time.value !== "all") {
      urlParams.set("t", filters.filter_time.value);
    }
    if (filters.filter_cat.value !== "all") {
      urlParams.set("cat", filters.filter_cat.value);
    }
    if (filters.filter_rating.value !== "all") {
      urlParams.set("rating", filters.filter_rating.value);
    }
    if (filters.filter_search.value !== "") {
      urlParams.set("q", filters.filter_search.value.join("||"));
    }

    if (filters.filter_price.value !== "all") {
      urlParams.set("price", filters.filter_price.value);
    }
    if (filters.filter_iarc.value !== "all") {
      urlParams.set("iarc", filters.filter_iarc.value);
    }
    urlParams.set("sort", activeSort.key);
    urlParams.set("sortdir", activeSort.dir);

    let filterWidth = [];
    let filterWithout = [];

    for (const [key, value] of Object.entries(filters.filter_feature)) {
      if (value === 1) {
        filterWidth.push(key);
      } else if (value === -1) {
        filterWithout.push(key);
      }
    }
    if (filterWidth.length !== 0) {
      urlParams.set("with", filterWidth.join(","));
    }
    if (filterWithout.length !== 0) {
      urlParams.set("without", filterWidth.join(","));
    }

    window.history.replaceState(
      1,
      "vrsort.com",
      window.location.href.split("?")[0] + "?" + urlParams
    );
  }

  const [sidebarOpen, setSidebarOpen] = useState(false);

  const results = useQuery(["data"], fetchApps);

  if (results.isLoading) {
    return <div>loading...</div>;
  }

  let genres = results.data.metadata.genres;
  let multiplayer = results.data.metadata.multiplayer;

  for (let i = 0; i < genres.length; ++i) {
    genres[i].size = 0;
  }
  for (let i = 0; i < multiplayer.length; ++i) {
    multiplayer[i].size = 0;
  }
  const apps = results.data.apps.filter((app) => {
    return app["price"] >= 0;
  });

  apps.sort((a, b) => {
    const akey = a[activeSort.key] * activeSort.dir;
    const bkey = b[activeSort.key] * activeSort.dir;

    if (akey === bkey) {
      const name_a = a.title;
      const name_b = b.title;

      return name_a.localeCompare(name_b);
    }

    if (akey < bkey) {
      return -1;
    }
    return 1;
  });

  const appsFiltered = apps.filter((app) => {
    let ok = true;

    if (filters.filter_rating.value !== "all") {
      if (app.rating < filters.filter_rating.value) {
        ok = false;
      }
    }

    if (filters.filter_price.value !== "all") {
      if (filters.filter_price.value === "free" && +app.price !== 0) {
        ok = false;
      }
      if (filters.filter_price.value === "paid" && +app.price === 0) {
        ok = false;
      }
    }

    if (filters.filter_iarc.value !== "all") {
      if (filters.filter_iarc.value === 100) {
        if (app.iarc_rating !== "PARENTAL GUIDANCE RECOMMENDED") {
          ok = false;
        }
      } else {
        if (app.iarc_rating === "PARENTAL GUIDANCE RECOMMENDED") {
          ok = false;
        } else {
          if (
            filters.filter_iarc.value >
            +app.iarc_rating.substring(0, app.iarc_rating.length - 1)
          ) {
            ok = false;
          }
        }
      }
    }

    if (filters.filter_input.value !== "all") {
      if (!app.input.includes(filters.filter_input.value)) {
        ok = false;
      }
    }

    if (filters.filter_search.value !== "") {
      console.log(filters.filter_search.value);
      let occurrences = 0;
      for (let i = 0; i < filters.filter_search.value.length; ++i) {
        if (
          app.title
            .toLowerCase()
            .includes(filters.filter_search.value[i].toLowerCase())
        ) {
          occurrences += 1;
          break;
        }
      }

      if (occurrences === 0) {
        ok = false;
      }
    }

    if (filters.filter_time.value !== "all") {
      if (app.release_date < filter_time_times[filters.filter_time.value]) {
        ok = false;
      }
    }
    if (filters.filter_cat.value !== "all") {
      if (app.category !== filters.filter_cat.value) {
        ok = false;
      }
    }

    Object.entries(filters.filter_feature).forEach((entry) => {
      const [key, value] = entry;

      if (value === -1) {
        if (app.features.includes(key)) {
          ok = false;
        }
      }

      if (value === 1) {
        if (!app.features.includes(key)) {
          ok = false;
        }
      }
    });

    if (ok) {
      for (let i = 0; i < genres.length; ++i) {
        if (app.features.includes(genres[i].filtername)) {
          genres[i].size += 1;
        }
      }
      for (let i = 0; i < multiplayer.length; ++i) {
        if (app.features.includes(multiplayer[i].filtername)) {
          multiplayer[i].size += 1;
        }
      }
    }
    return ok;
  });

  return (
    <>
      <div className="applist_parent">
        <div
          id="side_bar_mobile_open"
          onClick={() => {
            setSidebarOpen(!sidebarOpen);
          }}
          className={sidebarOpen ? "visible" : ""}
        ></div>

        <AppList questBrowser={questBrowser} apps={appsFiltered}>
          <Header>
            <Sort
              setSort={setSort}
              sortLable="Sort by"
              options={sortOptions}
              activeSort={activeSort}
            />
          </Header>{" "}
          <h1 className="results_size">
            <span className="results_size_number">{appsFiltered.length}</span>
            &nbsp;results
          </h1>
        </AppList>
        <div id="side_bar" className={sidebarOpen ? "visible" : ""}>
          <div
            id="reset_filters"
            className={
              compareObjects(filters, defaultFilters) &&
              compareObjects(activeSort, defaultSort)
                ? "hidden"
                : ""
            }
            onClick={() => {
              setFilters(defaultFilters);
            }}
          >
            Reset Filters
          </div>
          <FilterOneChoice
            filters={filters}
            setFilters={setFilters}
            filterTitle="Category"
            filterId="filter_cat"
            filterItems={categoriesOptions}
          />
          <FilterOneChoice
            filters={filters}
            setFilters={setFilters}
            filterTitle="Release Date"
            filterId="filter_time"
            filterItems={filter_time_options}
          />
          <FilterOneChoice
            filters={filters}
            setFilters={setFilters}
            filterTitle="Price"
            filterId="filter_price"
            filterItems={[
              { value: "all", name: "All" },
              { value: "free", name: "Free" },
              { value: "paid", name: "Paid" },
            ]}
          />
          <FilterOneChoice
            filters={filters}
            setFilters={setFilters}
            filterTitle="IARC rating"
            filterId="filter_iarc"
            filterItems={[
              { value: "all", name: "All" },
              { value: 3, name: "3+" },
              { value: 7, name: "7+" },
              { value: 12, name: "12+" },
              { value: 16, name: "16+" },
              { value: 18, name: "18+" },
              {
                value: 100,
                name: "Parental Guidance",
              },
            ]}
          />
          <FilterOneChoice
            filters={filters}
            setFilters={setFilters}
            filterTitle="Input"
            filterId="filter_input"
            filterItems={[
              { value: "all", name: "All" },
              { value: "Touch (as Gamepad)", name: "Touch (as Gamepad)" },
              { value: "Hand Tracking", name: "Hand Tracking" },
              { value: "Tracked Keyboard", name: "Tracked Keyboard" },
              { value: "Gamepad", name: "Gamepad" },
              { value: "Touch Controllers", name: "Touch Controllers" },
            ]}
            activeItemIndex={0}
          />
          <FilterAddRemove
            filters={filters}
            setFilters={setFilters}
            filterTitle="Narrow by genre"
            filterId="filter_feature"
            filterItems={genres}
          />{" "}
          <FilterAddRemove
            filters={filters}
            setFilters={setFilters}
            filterTitle="Multiplayer"
            filterId="filter_feature"
            filterItems={multiplayer}
          />
          <FilterOneChoice
            filters={filters}
            setFilters={setFilters}
            filterTitle="Rating"
            filterId="filter_rating"
            filterItems={[
              { value: "all", name: "All" },
              { value: 5, name: "5" },
              { value: 4, name: " 4" },
              { value: 3, name: " 3" },
            ]}
            activeItemIndex={0}
          />
        </div>
      </div>
    </>
  );
}

function App() {
  return (
    <QueryClientProvider client={queryClient}>
      <AppChild />
    </QueryClientProvider>
  );
}

export default App;
