import { useState } from "react";

const Sort = ({ sortLable, options, activeSort, setSort }) => {
  const [open, setOpen] = useState(false);
  return (
    <div id="sorting">
      <span id="sorting_lable">{sortLable}</span>
      <div id="sort_by">
        <div
          className={`sort_by_trigger noselect ${open ? "open" : ""}`}
          onClick={() => {
            setOpen(!open);
          }}
        >
          <div id="active_sort_name">{activeSort.name}</div>

          <svg
            className="sort_by_arrow"
            width="11"
            height="6"
            viewBox="0 0 11 6"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path d="M5.5 5.5L0 0H11L5.5 5.5Z"></path>
          </svg>
        </div>

        <div className={`sort_by_options noselect ${open ? "visible" : ""}`}>
          {options.map((item, index) => {
            return (
              <div
                key={"sort_" + index}
                className="sort_by_option"
                data-sort={item.key}
                data-sortdir={item.dir}
                onClick={() => {
                  setSort(item);
                  setOpen(false);
                }}
              >
                {item.name}
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};
export default Sort;
