const FilterAddRemove = ({
  filterTitle,
  filterId,
  filterItems,
  filters,
  setFilters,
}) => {
  const srotedFilterItems = filterItems.sort((a, b) => {
    let keyA = a.size;
    let keyB = b.size;

    if (filters[filterId][a.filtername] === -1) {
      keyA += 100000;
    }
    if (filters[filterId][b.filtername] === -1) {
      keyB += 100000;
    }

    if (keyA < keyB) {
      return 1;
    }
    return -1;
  });

  const filteredFilterItems = srotedFilterItems.filter((item) => {
    return item.size > 0 || filters[filterId][item.filtername] === -1;
  });
  return (
    <div className="filter">
      <div className="filter_name">{filterTitle}</div>
      <div id={filterId}>
        {filteredFilterItems.map((item, index) => {
          if (item.name === "360 Experience (non-game)") {
            item.name = "360 Experience";
          }

          let className = "";
          if (filters[filterId][item.filtername] === 1) {
            className = "active";
          }
          if (filters[filterId][item.filtername] === -1) {
            className = "remove";
          }
          return (
            <div
              key={filterId + "_" + item.filtername}
              className={`add_remove ${className}`}
            >
              <div
                className="add"
                onClick={() => {
                  if (filters[filterId][item.filtername] === 1) {
                    let copy = { ...filters };
                    delete copy[filterId][item.filtername];
                    setFilters(copy);
                  } else {
                    setFilters({
                      ...filters,
                      [filterId]: {
                        ...filters[filterId],
                        [item.filtername]: 1,
                      },
                    });
                  }
                }}
              ></div>
              <div
                className="name_n_size"
                onClick={() => {
                  if (filters[filterId][item.filtername] === 1) {
                    let copy = { ...filters };
                    delete copy[filterId][item.filtername];
                    setFilters(copy);
                  } else {
                    setFilters({
                      ...filters,
                      [filterId]: {
                        ...filters[filterId],
                        [item.filtername]: 1,
                      },
                    });
                  }
                }}
              >
                <div className="name">{item.name}</div>
                <div className="size">{item.size}</div>
              </div>
              <div
                className="remove"
                onClick={() => {
                  if (filters[filterId][item.filtername] === -1) {
                    const copy = { ...filters };
                    delete copy[filterId][item.filtername];
                    setFilters(copy);
                  } else {
                    setFilters({
                      ...filters,
                      [filterId]: {
                        ...filters[filterId],
                        [item.filtername]: -1,
                      },
                    });
                  }
                }}
              ></div>
            </div>
          );
        })}
      </div>
    </div>
  );
};
export default FilterAddRemove;
