import AppImage from "./AppImage";
import Price from "./Price";
import Size from "./Size";
import Stars from "./Stars";
import Tags from "./Tags";

const Card = (props) => {
  return (
    <a
      target="_blank"
      rel="noreferrer"
      href={`https://www.oculus.com/experiences/quest/${props.id}?utm_source=vrsort.com`}
      id={props.divId}
      className="app"
      style={props.style}
      key={props.id}
    >
      <AppImage
        link={props.cover}
        linkReddit={props.rthumb}
        id={props.id}
        isMobile={props.isMobile}
      />
      <div className="app_info">
        <div className="app_info_left">
          <h3>{props.title}</h3>

          <Stars value={props.rating} number={props.reviews} />
          <Tags list={props.features} />
        </div>
        <div className="app_info_right">
          <Price value={props.price} />
          <Size bytes={props.size} />
        </div>
      </div>
    </a>
  );
};

export default Card;

/*
['id', 'title', 'price',
'size', 'time', 'rating',
'reviews', 'features', 'cover', 'description', 'trailer', 'category', 'images', 'release_date', 'updated', 'update_info']
*/
